<template>
    <div>
        <h3></h3>
        <a-card>
            <a-row :gutter="20" style="margin-bottom: 10px">
                <a-col :span="4">
                    <a-button type="primary" @click="addCategoryVisible = true"></a-button>
                </a-col>
            </a-row>

            <a-table
                rowKey="ID"
                :columns="columns"
                :pagination="pagination"
                :dataSource="categorylist"
                bordered
                @change="handleTableChange"
            >
                <template slot="action" slot-scope="id">
                    <div class="actionSlot">
                        <a-button type="primary" icon="edit" style="margin-right: 15px" @click="editCategory(id)"
                            ></a-button
                        >
                        <a-button style="margin-right: 15px" icon="delete" type="danger" @click="deleteCategory(id)"
                            ></a-button
                        >
                    </div>
                </template>
            </a-table>
        </a-card>
        <!--  -->
        <a-modal
            closable
            :visible="addCategoryVisible"
            width="60%"
            title=""
            @ok="addCategoryOk"
            @cancel="addCategoryCancel"
            :destroyOnClose="true"
        >
            <a-form-model :model="newCategory" :rules="addCategoryRules" ref="addCategoryRef">
                <a-form-model-item has-feedback label="" prop="name">
                    <a-input v-model="newCategory.name"></a-input>
                </a-form-model-item>
            </a-form-model>
        </a-modal>
        <!--  -->
        <a-modal
            closable
            :visible="editCategoryVisible"
            width="60%"
            title=""
            @ok="editCategoryOk"
            @cancel="editCategoryCancel"
        >
            <a-form-model :model="category" :rules="categoryRules" ref="editCategoryRef">
                <a-form-model-item has-feedback label="" prop="name">
                    <a-input v-model="category.name"></a-input>
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>

<script>
const columns = [
    {
        title: 'ID',
        dataIndex: 'ID',
        width: '10%',
        key: 'id',
        align: 'center'
    },
    {
        title: '',
        dataIndex: 'name',
        width: '30%',
        key: 'name',
        align: 'center'
    },
    {
        title: '',
        width: '40%',
        key: 'action',
        dataIndex: 'ID',
        scopedSlots: { customRender: 'action' },
        align: 'center'
    }
]
export default {
    data() {
        return {
            categorylist: [],
            columns,
            pagination: {
                pageSizeOptions: ['5', '10', '20'],
                total: 0,
                showSizeChanger: true,
                showTotal: (total) => `${total}`,
                current: 1,
                pageSize: 5
            },
            queryParam: {
                pageSize: 5,
                page: 1
            },
            newCategory: {
                ID: 0,
                name: ''
            },
            category: {
                ID: 0,
                name: ''
            },
            addCategoryVisible: false,
            editCategoryVisible: false,
            categoryRules: {
                name: [
                    {
                        validator: (rule, value, callback) => {
                            if (this.category.name === '') {
                                callback(new Error(''))
                            }
                            if ([...this.category.name].length < 4 || [...this.category.name].length > 12) {
                                callback(new Error('412'))
                            } else {
                                callback()
                            }
                        },
                        trigger: 'blur'
                    }
                ]
            },
            addCategoryRules: {
                name: [
                    {
                        validator: (rule, value, callback) => {
                            if (this.newCategory.name === '') {
                                callback(new Error(''))
                            }
                            if ([...this.newCategory.name].length < 4 || [...this.newCategory.name].length > 12) {
                                callback(new Error('412'))
                            } else {
                                callback()
                            }
                        },
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    created() {
        this.indexCategory()
    },
    methods: {
        // 
        async indexCategory() {
            const { data: res } = await this.$http.get('categories', {
                params: {
                    pageSize: this.queryParam.pageSize,
                    page: this.queryParam.page
                }
            })
            if (res.status !== 0) return this.$message.error(res.message)
            this.categorylist = res.data.categories
            this.pagination.total = res.data.total
            if (
                this.pagination.current > 1 &&
                (this.pagination.current - 1) * this.pagination.pageSize >= this.pagination.total
            ) {
                this.pagination.current -= 1
                this.queryParam.page = this.pagination.current
                await this.indexCategory()
            }
        },
        // 
        handleTableChange(pagination, filters, sorter) {
            const pager = { ...this.pagination }
            pager.current = pagination.current
            pager.pageSize = pagination.pageSize
            this.queryParam.pageSize = pagination.pageSize
            this.queryParam.page = pagination.current
            if (pagination.pageSize !== this.pagination.pageSize) {
                this.queryParam.page = 1
                pager.current = 1
            }
            this.pagination = pager
            this.indexCategory()
        },
        // 
        deleteCategory(id) {
            this.$confirm({
                title: '',
                content: ',',
                onOk: async () => {
                    const { data: res } = await this.$http.delete(`categories/${id}`)
                    if (res.status !== 0) {
                        return this.$message.error(res.message)
                    }
                    this.$message.success('')
                    this.indexCategory()
                },
                onCancel: () => {
                    this.$message.info('')
                }
            })
        },
        // 
        addCategoryOk() {
            this.$refs.addCategoryRef.validate(async (valid) => {
                if (!valid) {
                    return this.$message.error(',')
                }
                const { data: res } = await this.$http.post('categories', {
                    name: this.newCategory.name
                })
                if (res.status !== 0) {
                    this.$message.error(res.message)
                } else {
                    this.addCategoryVisible = false
                    this.$message.success('')
                    await this.indexCategory()
                }
            })
        },
        addCategoryCancel() {
            this.$refs.addCategoryRef.resetFields()
            this.addCategoryVisible = false
        },
        async editCategory(id) {
            this.editCategoryVisible = true
            const { data: res } = await this.$http.get(`categories/${id}`)
            if (res.status !== 0) {
                return this.$message.error(res.message)
            }
            this.category = res.data.category
        },
        editCategoryOk() {
            this.$refs.editCategoryRef.validate(async (valid) => {
                if (!valid) {
                    return this.$message.error(',')
                }

                const { data: res } = await this.$http.put(`categories/${this.category.ID}`, {
                    name: this.category.name
                })
                if (res.status !== 0) {
                    this.$message.error(res.message)
                } else {
                    this.$message.success('')
                    this.indexCategory()
                    this.editCategoryVisible = false
                }
            })
        },
        editCategoryCancel() {
            this.editCategoryVisible = false
            this.$message.info('')
        }
    }
}
</script>

<style scoped>
.actionSlot {
    display: flex;
    justify-content: center;
}
</style>
